* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button {
  background-color: rgb(39, 39, 39);
  color: rgb(202, 219, 218);
  font-size: 1.5rem;
  padding: .75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color .5s ease;
}

button:hover {
  background-color: rgb(0, 253, 253);
  box-shadow: 0px 0px 10px rgba(0, 253, 253, 0.5);
  transition: all .5s ease;
  color: rgb(39, 39, 39);
}

input {
  font-size: 1.5rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  color: rgb(0, 253, 253);
  background-color: black;
  font-family: monospace;
  text-shadow: 0px 0px 10px rgba(0, 253, 253, 0.5);
  width: 90%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgb(34, 34, 34);
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  padding: .7rem;
  border-radius: 0.5rem;
  background-color: rgb(14, 14, 14);
}

.calculator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  border-radius: 0.5rem;
  background-image: linear-gradient(to bottom, rgb(27, 27, 27), rgb(34, 29, 29));
  border: solid 2px rgb(0, 253, 253);
  
}

.operator {
  
  color: rgb(0, 253, 253);
  font-size: 1.5rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color .5s ease;
}

.operator:hover {
  background-color: rgb(0, 253, 253);
  box-shadow: 0px 0px 10px rgba(0, 253, 253, 0.5);
  transition: all .5s ease;
  color: rgb(39, 39, 39);
}

.Special {
  background-color: rgb(0, 253, 253);
  color: rgb(39, 39, 39);
  font-size: 1.5rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color .5s ease;
}

@media screen and (max-width: 768px) {
  .keypad {
    grid-template-columns: repeat(4, 1fr);
  }
  .calculator {
    width: 100%;
  }

  input {
    width: 100%;
  }
}

